import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import Button from '../components/Button.jsx';
import GMap from '../components/Map.jsx';
import Icon from '../components/Icon.jsx';
import getText from '../utils/getText.js';
import { Header,
  Grid, Container, Form, Input, TextArea, Image, Sticky } from 'semantic-ui-react';
import FontAwesome from 'react-fontawesome';
import image from '../../assets/images/map-placeholder.png';

export default class Contact extends React.Component {
  render() {
    return <div id='contact' className='page'>
             <section>
               <div ref={ element => {
                            this.stickyContext = element
                          } }>
                 <Grid stackable columns='equal'>
                   <Grid.Column className='map-column'>
                     <div className='map-container'>
                       <div className='map'>
                         <GMap />
                       </div>
                     </div>
                   </Grid.Column>
                   <Grid.Column className='contact-info'>
                     <div className="content">
                       <h3 className='heading'>{ getText('info.name') }</h3>
                       <div className="info">
                         <div>
                           <Icon height='60px' image='pin' />
                         </div>
                         <div>
                           { getText('info.address') }
                         </div>
                       </div>
                       <div className="info">
                         <div>
                           <Icon height='60px' image='phone' />
                         </div>
                         <div>
                           { getText('info.phone') }
                         </div>
                       </div>
                       <div className="info">
                         <div>
                           <Icon height='45px' image='envelope' />
                         </div>
                         <div>
                           { getText('info.email') }
                         </div>
                       </div>
                       <Button type='appointment' />
                     </div>
                   </Grid.Column>
                 </Grid>
               </div>
             </section>
           </div>
  }
}



